.content-body {
    &.ppMain-content {
        .swiper-container {
            &.sub-content {
                margin-bottom: 33px;
            }
            .moreTitle-wrapper {
                @include flexCenter;
                position: relative;
                margin-bottom: 16px;
                >img {
                    margin-right: 5px;
                }
                p {
                    @include fontSize(17px,700);
                    line-height: 25px;
                }
                button {
                    position: absolute;
                    right: 0;
                    top: 0;
                    height: 25px;
                    @include flexCenter;
                    @include btnNone;
                }
            }
        }
        .mainSlide-container {
            margin-bottom: 18px;
            .swiper-wrapper {
                .swiper-slide {
                    height: 376px;
                    background: url(../img/image_main.png) center center;
                    background-size: cover;
                    position: relative;
                    .slogun-box {
                        position: absolute;
                        left: 35px;
                        bottom: 83px;
                        ul {
                            margin-bottom: 15px;
                            li {
                                line-height: 40px;
                                @include fontSize(33px,700);
                                color: #fff;
                            }
                        }
                        p {
                            color: #fff;
                            line-height: 25px;
                            @include fontSize(15px,300);
                        }
                    }
                }
            }
            .swiper-pagination {
                width: calc( 100% - 30px );
                &.swiper-pagination-progressbar {
                    top: inherit;
                    bottom: 40px;
                    left: 30px;
                    height: 3px;
                    background: rgba(255,255,255,.5);
                    .swiper-pagination-progressbar-fill {
                        background: #fff;
                    }
                }
            }
        }
        .storeTitle-container {
            padding: 0 15px;
            margin-bottom: 20px;
            .ico-wrapper {
                @include flexCenter;
                button {
                    @include btnNone;
                    @include flexAllCenter;
                    margin-right: 5px;
                    &:last-of-type {
                        margin-right: 0;
                    }
                }
                .counting-btn {
                    position: relative;
                    span {
                        position: absolute;
                        top: -3px;
                        right: -7px;
                        @include fontSize(10px,700);
                        color: #fff;
                        background: $mainColor;
                        padding: 0px 6px;
                        border-radius: 99px;
                    }
                }
            }
            .title-wrapper {
                .main-title {
                    &:after {
                        @include clearBoth;
                    }
                    p {
                        float: left;
                        @include fontSize(23px,700);
                        line-height: 34px;
                        max-width: calc( 100% - 110px );
                        @include wordClear;
                    }
                    ul {
                        margin-left: 10px;
                        padding-top: 7px;
                        @include flexCenter;
                        float: left;
                        li {
                            border: 1px solid #000;
                            padding: 1px 3px;
                            margin-right: 5px;
                            @include fontSize(11px,300);
                            line-height: 17px;
                            &:last-of-type {
                                margin-right: 0;
                            }
                            &.gray-link {
                                border-color: rgba(0,0,0,.2);
                                color: #333;
                            }
                            &.red-link {
                                border-color: rgba($mainColor,.5);
                                color: $mainColor;
                            }
                        }
                    }
                }
                .sub-title {
                    ul {
                        @include flexCenter;
                        li {
                            line-height: 18px;
                            @include fontSize(12px,400);
                            color: #999;
                            margin-right: 21px;
                            position: relative;
                            &:after {
                                content: "";
                                position: absolute;
                                right: -11px;
                                width: 1px;
                                height: 7px;
                                background: #999;
                                top: 50%;
                                transform: translate(0,-50%);
                            }
                            &:last-of-type {
                                margin-right: 0;
                                &:after {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
        }
        .itemSlide-container {
            padding: 0 20px;
            .swiper-wrapper {
                .swiper-slide {
                    width: 150px;
                    .img-box {
                        margin-bottom: 10px;
                        background: url(../img/image_01.png) center center;
                        background-size: cover;
                        height: 150px;
                        position: relative;
                        img {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                        }
                    }
                    .price-box {
                        margin-bottom: 3px;
                        p {
                            span {
                                line-height: 18px;
                                @include fontSize(15px,700);
                                &.sale-word {
                                    color: $mainColor;
                                    margin-right: 5px;
                                }
                            }
                            small {
                                @include fontSize(10px,300);
                                text-decoration-line: line-through;
                                color: #333;
                                text-decoration-color: #333;
                                margin-left: 2px;
                            }
                        }
                    }
                    .item-subject {
                        p {
                            width: 100%;
                            line-height: 18px;
                            color: #333;
                            @include fontSize(12px,400);
                            @include wordClear;
                        }
                    }
                }
            }
        }
        .wideSlide-container {
            padding: 0 20px;
            .swiper-wrapper {
                .swiper-slide {
                    width: 290px;
                    position: relative;
                    .img-box {
                        background: url(../img/image_04.png) center center;
                        background-size: cover;
                        height: 230px;
                        position: relative;
                        img {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                        }
                    }
                    .word-box {
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        background: #fff;
                        padding: 10px 14px 5px 5px;
                        max-width: 180px;
                        .price-box {
                            margin-bottom: 3px;
                            p {
                                span {
                                    line-height: 18px;
                                    @include fontSize(15px,700);
                                    &.sale-word {
                                        color: $mainColor;
                                        margin-right: 5px;
                                    }
                                }
                                small {
                                    @include fontSize(10px,300);
                                    text-decoration-line: line-through;
                                    color: #333;
                                    text-decoration-color: #333;
                                    margin-left: 2px;
                                }
                            }
                        }
                        .item-subject {
                            p {
                                width: 100%;
                                line-height: 18px;
                                color: #333;
                                @include fontSize(12px,400);
                                @include wordClear;
                            }
                        }
                    }
                }
            }
        }
        .cubeItem-container {
            padding: 0 20px;
            .cubeBox-wrapper {
                @include flexCenter;
                .cubeItem {
                    width: calc( (100% - 20px)/3 );
                    margin-right: 10px;
                    &:last-of-type {
                        margin-right: 0;
                    }
                    .img-box {
                        margin-bottom: 10px;
                        background: url(../img/image_06.png) center center;
                        background-size: cover;
                        position: relative;
                        img {
                            position: absolute;
                            right: 0;
                            bottom: 0;
                        }
                    }
                    .price-box {
                        margin-bottom: 3px;
                        p {
                            span {
                                line-height: 18px;
                                @include fontSize(15px,700);
                                &.sale-word {
                                    color: $mainColor;
                                    margin-right: 5px;
                                }
                            }
                            small {
                                @include fontSize(10px,300);
                                text-decoration-line: line-through;
                                color: #333;
                                text-decoration-color: #333;
                                margin-left: 2px;
                            }
                        }
                    }
                    .item-subject {
                        p {
                            width: 100%;
                            line-height: 18px;
                            color: #333;
                            @include fontSize(12px,400);
                            @include wordClear;
                        }
                    }
                }
            }
        }
        .reviewSlide-container {
            padding: 0 20px;
            .swiper-wrapper {
                .swiper-slide {
                    width: 310px;
                    position: relative;
                    .review-list {
                        padding-bottom: 15px;
                        &:last-of-type {
                            padding-top: 15px;
                            border-top: 1px solid #eee;
                        }
                        &:after {
                            @include clearBoth;
                        }
                        .img-box {
                            background: url(../img/image_09.png) center center;
                            background-size: cover;
                            width: 97px;
                            height: 97px;
                            margin-right: 15px;
                            float: left;
                        }
                        .word-box {
                            float: left;
                            width: calc( 100% - 112px );
                            padding-left: 14px;
                            height: 97px;
                            position: relative;
                            >img {
                                position: absolute;
                                top: 0;
                                left: 0;
                            }
                            .user-word {
                                display: -webkit-box;
                                -webkit-box-orient: vertical;
                                -webkit-line-clamp: 3;
                                overflow: hidden;
                                color: #333;
                                @include fontSize(12px,400);
                                line-height: 20px;
                            }
                            .toBottom {
                                position: absolute;
                                bottom: -4px;
                                right: 0;
                                width: calc( 100% - 14px );
                                .user-name {
                                    @include fontSize(10px,300);
                                    line-height: 18px;
                                    strong,span {
                                        @include fontSize(12px,700);
                                    }
                                    strong {
                                        margin-right: 1px;
                                    }
                                }
                                .item-subject {
                                    @include fontSize(10px,400);
                                    line-height: 15px;
                                    width: 100%;
                                    @include wordClear;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}


.fullPopup-wrapper {
    display: none;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 11;
    &.active {
        display: block;
    }
    .header-section {
        height: 54px;
        padding-left: 17px;
        @include flexCenter;
        position: relative;
        p {
            line-height: 29px;
            @include fontSize(20px,500);
        }
        button {
            position: absolute;
            right: 10px;
            top: 50%;
            transform: translate(0,-50%);
            @include btnNone;
            width: 29px;
            height: 29px;
            @include flexAllCenter;
        }
    }
    .tab-section {
        .target--wrap {
            .tabTarget {
                ul {
                    li {
                        select:required:invalid {
                            color: gray;
                          }
                          option[value=""][disabled] {
                            display: none;
                          }
                          option {
                            color: red;
                          }
                    }
                }
            }
        }
    }
}