.content-body {
    &.ppGoods-content {
        .goodsList-container {
            .goods-wrapper {
                @include flexCenter;
                padding: 20px 0;
                position: relative;
                &:after {
                    content: "";
                    width: calc( 100% + 30px );
                    position: absolute;
                    left: -15px;
                    bottom: 0;
                    height: 1px;
                    background: #eee;
                }
                &:first-of-type {
                    padding-top: 0;
                }
                .img-box {
                    background: url(../img/image_09.png) center center;
                    background-size: cover;
                    width: 80px;
                    height: 80px;
                    margin-right: 10px;
                }
                .word-box {
                    height: 80px;
                    width: calc( 100% - 90px );
                    position: relative;
                    .codeNum {
                        @include fontSize(12px,400);
                        color: #333;
                        line-height: 14px;
                        margin-bottom: 7px;
                    }
                    .goodsSub {
                        p {
                            @include fontSize(14px,500);
                            color: #333;
                            line-height: 20px;
                            @include flexCenter;
                            @include wordClear;
                            width: 100%;
                            span {
                                display: inline-block;
                                color: #fff;
                                @include fontSize(12px,300);
                                line-height: 18px;
                                background: #000;
                                padding: 3px 9px;
                                border-radius: 99px;
                                margin-right: 5px;
                            }
                        }
                    }
                    .toBottom {
                        position: absolute;
                        width: 100%;
                        bottom: -2px;
                        &:after {
                            @include clearBoth;
                        }
                        .counting {
                            float: left;
                            color: $mainColor;
                            @include fontSize(12px,500);
                            line-height: 18px;
                            span {
                                color: $mainColor;
                                @include fontSize(12px,500);
                                margin-left: 3px;
                            }
                        }
                        .priceNum {
                            float: right;
                            @include fontSize(12px,400);
                            color: #333;
                            span {
                                @include fontSize(15px,700);
                            }
                        }
                    }
                }
            }
        }
    }
}